.contact__head{
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    color: #fff;
    font-size: 100px;
    font-weight: 700;
    padding: 0px 08vw;
}

.contact__dataform {
    padding: 50px 08vw;
    background-color: #f9f9f9;
}

.contact__dataform__data {
    display: flex;
    gap: 20px;
    align-items: center;
}

.contact__dataform__data__address {
    flex: 1;
}

.contact__dataform__data__address__tit {
    font-size: 35px;
    color: #243E63;
    font-weight: 700;
    margin-bottom: 20px;
}

.contact__dataform__data__address__indicator {
    font-size: 20px;
    color: #243E63;
    font-weight: 500;
}

.contact__dataform__data__address__val {
    font-size: 16px;
    color: #243E63;
}

.contact__dataform__data__img {
    flex: 1;
}

.contact__dataform__data__img__ima {    
    width: 100%;
    object-fit: contain;
}

.contact__dataform__form {
    display: flex;
    gap: 20px;
    margin: 50px 0px;
}

.contact__dataform__form__container {
    flex: 1.5;
    flex-direction: column;
}

.contact__dataform__form__empty {
    flex: 1;
}

.input__class {
    width: 100%;
    outline: none !important;
    border-color: #719ECE;
    border-width: 0.5px;
    margin: 10px 0px;
    padding: 20px 30px;
    border-radius: 200px;
    font-size: 16px;
}

.textare__class {
    width: 100%;
    outline: none !important;
    border-color: #719ECE;
    border-width: 0.5px;
    margin: 10px 0px;
    padding: 20px 30px;
    border-radius: 20px;
    font-size: 16px;
}

.contact__dataform__form__container__but {    
    margin-top: 60px;
    padding: 10px 50px;
    background-color: #18C772;
    color: #fff;
    font-size: 25px;
    cursor: pointer;
    border-radius: 20px 50px 20px;
}

.contact__dataform__form__container__but:hover {
    padding-top: 4px;
    padding-bottom: 16px;
}

@media only screen and (max-width: 800px) {
    .contact__head {
        font-size: 55px;
    }

    .contact__dataform__data {
        flex-direction: column;
    }

    .contact__dataform__form__empty {
        flex: 0.2;
    }
}

@media only screen and (max-width: 500px) {
    .contact__head {
        font-size: 45px;
    }
}

@media only screen and (max-width: 350px) {
    .contact__head {
        font-size: 40px;
    }
}

@media only screen and (max-width: 300px) {
    .contact__head {
        font-size: 35px;
    }
}