.service__head{
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    color: #fff;
    font-size: 100px;
    font-weight: 700;
    padding: 0px 08vw;
}

.service__provide {
    background-color: #fff;
    padding: 100px 05vw;
}

.service__provide__head {
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
    text-align: center;
}

.service__provide__mess {
    color: #243E63;
    font-size: 18px;
    text-align: center;
}

.service__diff {
    flex: 1;
    padding: 0px 05vw;
}

.services__diff__container {
    display: flex;
    width: 100%;
    padding: 28vh 0px;
    align-items: center;
    background-image: url("../../assets/images/back5.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.service__diff__head {  
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
    text-align: left;
    margin-bottom: 20px;
}

.service__diff__point__mess {
    color: #243E63;
    font-size: 16px;
    text-align: justify;
}

.service__diff__emty{
    flex: 1;
}

@media only screen and (max-width: 800px) {
    .service__head {
        font-size: 55px;
    }

    .services__diff__container {
        flex-direction: column;
    }
    .service__diff__emty {
        display: none;
    }
}

@media only screen and (max-width: 400px) {
    .service__head {
        font-size: 50px;
    }
}

@media only screen and (max-width: 300px) {
    .service__head {
        font-size: 40px;
    }
}